<template>
  <div class="testProject">
    <div class="function">
      <el-row>
        <el-col :span="4">
          <div class="grid-content bg-purple">
            <el-input
              placeholder="ID"
              icon="search"
              v-model="input">
            </el-input>
          </div>
        </el-col>
        <el-col :span="20">
          <el-button type="primary" @click.native="onNewClick"></el-button>
        </el-col>
      </el-row>
    </div>
    <div id="table">
      <el-table
        :data="tableData"
        stripe
        style="width: 100%">
        <el-table-column
          prop="id"
          label="ID">
        </el-table-column>
        <el-table-column
          prop="testName"
          label="">
        </el-table-column>
        <el-table-column
          prop="types"
          label="">
        </el-table-column>
        <el-table-column
          prop="testUser"
          label=""
          width=140>
        </el-table-column>
        <el-table-column
          prop="projectID"
          label="ID"
          width=180>
        </el-table-column>
        <el-table-column
          prop="create"
          label="">
        </el-table-column>
        <el-table-column
          prop="lastUpdate"
          label=""
          width=150>
        </el-table-column>
        <el-table-column
          prop="status"
          label="">
        </el-table-column>

        <el-table-column
          :context="_self"
          inline-template
          label=""
          width=120>
          <span>
            <el-button @click="onEditClick($index)" type="text" size="small"></el-button>
            <el-button @click="onDelClick($index)" type="text" size="small"></el-button>
          </span>
        </el-table-column>
      </el-table>
    </div>
    <el-dialog :title="dialogTitle" v-model="isDialogShow" size="small">
      <el-form :model="form">
       <el-form-item label="" :label-width="formLabelWidth">
         <el-input v-model="form.name" auto-complete="off"></el-input>
       </el-form-item>
       <el-form-item label="" :label-width="formLabelWidth">
         <el-select v-model="form.region" placeholder="">
           <el-option label="" value="shanghai"></el-option>
           <el-option label="" value="beijing"></el-option>
         </el-select>
       </el-form-item>
     </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="isDialogShow = false"> </el-button>
        <el-button type="primary" @click="isDialogShow = false"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  import tableData from '../../mock/testProject.js';
  export default {
    data(){
      return {
        input: '',
        dialogTitle: '',
        isDialogShow: false,
        tableData: tableData,
        form: {
         name: '',
         region: '',
         date1: '',
         date2: '',
         delivery: false,
         type: [],
         resource: '',
         desc: ''
       },
       formLabelWidth: '120px'

      }

    },
    methods: {
      // 
      onEditClick: function(index){
        this.dialogTitle = "";
        this.isDialogShow = true;
      },

      // 
      onDelClick: function(index){
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          // 
          this.tableData.splice(index,1);
          this.$message({
            type: 'success',
            message: '!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      },
      onNewClick: function(){
        this.dialogTitle = "";
        this.isDialogShow = true;
      }
    }
  }
</script>

<style media="screen">
  .function{
    margin-bottom:10px;
  }
  .function button{
    float:right;
  }
</style>



